@charset "UTF-8";

/* 비회원 및 Retail */
.main_container {
  #mainVisual {
    h2 {text-indent:-9999px; opacity:0; height:0;}
    .visual_wrap {
      overflow:hidden; position:relative;
      .swiper-slide {height:660px; padding:150px 0 0; box-sizing:border-box;}
      .slogan_wrap {max-width:1200px; margin:0 auto; box-sizing:border-box;}
      .visual_btn {
        position:absolute; top:480px; left:50%; z-index:2;
        button {
          @include textBackground(13px, 20px);
          @include backgroundSoltC();
        }
        &.swiper-button-disabled {opacity:0.4}
        &.visual_prev_btn {
          margin:0 0 0 -600px;
          button {background-image:url(../../images/main/visualBtn_prev.png)}
        }
        &.visual_next_btn {
          margin:0 0 0 -542px;
          button {background-image:url(../../images/main/visualBtn_next.png)}
        }
      }
      .visual_slide {
        color:$white;
        strong {font-size:68px; font-weight:$bold; display:block;}
        p {font-size:24px; font-weight:$medium; margin:35px 0 75px}
        .btn_wrap {
          font-size:0;
          a {
            font-size:16px; line-height:46px; padding:0 10px; min-width:180px; margin:0 8px 0 0; text-align:center; border-radius:50px; display:inline-block; color:$white; border:1px solid $white; box-sizing:border-box;
            @include transition(all, .3s);
            &.btn_bruchure {padding:0 31px;}
            &:hover, &:focus {
              background-color:rgba(255, 255, 255, 0.3);
            }
          }
        }
      }
    }
    .shourtcut {
      max-width:1200px; margin:0 auto; padding:50px 0; box-sizing:border-box; display:flex; justify-content:space-between;
      li {
        width:276px; margin:0 31px 0 0; position:relative; flex-wrap:nowrap;
        &:last-child {margin:0;}
        &:after {
          width:1px; height:44px; background-color:$gray2; position:absolute; top:50%; right:-15px; content:"";
          @include translate(0, -50%);
        }
        &:last-child:after {display:none;}
      }
      li:nth-of-type(1) a {background-image:url(../../images/main/icon_retail.png);}
      li:nth-of-type(2) a {background-image:url(../../images/main/icon_financial.png);}
      li:nth-of-type(3) a {background-image:url(../../images/main/icon_parts.png);}
      li:nth-of-type(4) a {background-image:url(../../images/main/icon_software.png);}
      a {
        display:block; padding:27px 0 27px 35px; box-sizing:border-box;
        @include backgroundSolt(95%);
        @include transition(all, .3s);
        strong, span {
          @include transition(all, .3s);
        }
        strong {font-size:19px; font-weight:$medium; color:$gray7; display:block;}
        span {
          font-size:15px; margin:8px 0 0; padding:0 23px 0 0; display:inline-block; color:$gray5; position:relative;
          &:after {
            width:16px; height:16px; background-image:url(../../images/main/icon_arrow_s.png); position:absolute; top:50%; right:0; content:"";
            @include translate(0, -50%);
            @include backgroundSoltC();
          }
        }
        &:hover, &:focus {
          @include backgroundSolt(90%);
          strong {color:$blue5;}
          span {color:$blue5;}
        }
      }
    }

    @include mob(){
      .visual_wrap {
        .swiper-slide {
          height:em(710); padding:0; position:relative;
          &:before {width:100%; height:100%; background-color:rgba(0, 0, 0, 0.5); content:""; position:absolute; top:0; left:0;}
        }
        .slogan_wrap {
          width:100%; padding:0; text-align:center; position:absolute; top:50%; left:0;
          @include translate(0, -50%);
        }
        .visual_btn {display:none;}
        .visual_slide {
          strong {font-size:em(80);}
          p {font-size:em(32); line-height:em(42); margin:em(50) em(30)}
          .btn_wrap {
            a {
              font-size:em(26); line-height:em(86); padding:0 em(36); min-width:em(310); margin:0 em(14) 0 0; border-radius:em(50); border-width:em(2);
              &.btn_bruchure {padding:0 em(36);}
              &.btn_more {background-color:$blue5; border-color:$blue5;}
            }
          }
        }
      }
      .shourtcut {
        padding:em(60) em(30); flex-wrap:wrap;
        li {
          width:50%; margin:0; border-bottom:1px solid $gray2; border-left:1px solid $gray2; box-sizing:border-box;
          &:after {display:none;}
          &:nth-of-type(2n+1) {border-left:none;}
          &:nth-of-type(3),&:nth-of-type(4) {border-bottom:none;}
        }
        li:nth-of-type(1) a {background-image:url(../../images/main/icon_retail_m.png);}
        li:nth-of-type(2) a {background-image:url(../../images/main/icon_financial_m.png);}
        li:nth-of-type(3) a {background-image:url(../../images/main/icon_parts_m.png);}
        li:nth-of-type(4) a {background-image:url(../../images/main/icon_software_m.png);}
        a {
          padding:27px 0 27px em(30); background-size:em(140);
          @include backgroundSolt(100%);
          strong {font-size:em(34); font-weight:$bold;}
          span {
            font-size:em(24); margin:em(15) 0 0; padding:0;
            &:after {display:none;}
          }
          &:hover, &:focus {
            @include backgroundSolt(100%);
          }
        }
      }
    }
  }
  #contents {
    background-color:$gray1;
    .title_wrap {
      padding:0 0 50px; text-align:center;
      h3 {font-size:42px; color:$gray7; font-weight:$medium;}
    }
    section {padding:70px 0;}
    .default_section {
      padding:70px 0 90px; overflow-x:hidden;
      > div:nth-of-type(3) {padding:145px 0 55px;}
      .slide_wrap {max-width:1200px; margin:0 auto; overflow:visible; box-sizing:border-box;}
      .swiper-slide {max-width:300px; margin:0 12px 0 0;}
      .item {
        a {display:block; border:1px solid $gray2; border-radius:10px; background-color:$white; box-sizing:border-box;}
        p {padding:40px 40px 20px; box-sizing:border-box;}
        strong {font-size:30px; font-weight:$medium; display:block; color:$gray7;}
        span {font-size:14px; line-height:22px; min-height:44px; margin:20px 0 0; color:$gray6; display:block;}
        img {width:100%;}
      }
    }
    .banner_section {
      padding:45px 0 40px; background-image:url(../../images/main/banner_bg.jpg); color:$white; text-align:center;
      @include backgroundSoltC();
      h3 {text-indent:-99999px; opacity:0; height:0;}
      .layout_wrap div {display:inline-block;}
      strong {font-size:28px; font-weight:$medium;}
      p {
        font-size:15px; padding:0 175px 0 0; margin:15px 0 0; opacity:0.4; position:relative;
        em {position:absolute; top:0; right:0;}
        br {display:none;}
      }
    }
    .Preferred_section {
      .layout_wrap {overflow:hidden;}
      .item {
        a {display:block; border:1px solid $gray2; border-radius:10px; background-color:$white; box-sizing:border-box;}
        em {
          display:block; font-weight:$bold;
          &.user_type01 {color:$blue6}
          &.user_type02 {color:$blue7}
        }
        strong {font-weight:$medium; display:block; color:$gray7;}
        span {color:$gray6; display:block;}
        img {width:100%; box-sizing:border-box;}
      }
      .item_list_new {
        .item {
          p {padding:25px 25px 10px;}
          em {font-size:12px;}
          strong {font-size:19px; padding:10px 0 15px;}
          span {font-size:12px; line-height:18px; min-height:36px;}
          img {padding:0 25px;}
        }
      }
      @media screen and (min-width: 1025px) {
        .swiper-wrapper {width:auto; height:auto; display:block;}
        .swiper-slide {height:auto;}
        .item_list_new {
          padding:0 0 0 40%; position:relative; transform:translate(0, 0) !important;
          .item {
            float:left; width:33.333% !important; margin:15px 0 0; overflow:hidden;
            a {width:98%;}
            &:nth-of-type(2), &:nth-of-type(5) {clear:both;}
            &:nth-of-type(2) a, &:nth-of-type(5) a {float:left;}
            &:nth-of-type(3) a, &:nth-of-type(6) a {margin:0 auto;}
            &:nth-of-type(4) a, &:nth-of-type(7) a {float:right;}
            &:nth-of-type(1), &:nth-of-type(2),&:nth-of-type(3),&:nth-of-type(4) {margin:0}
          }
          .item:first-child {
            position:absolute; top:0; left:0; width:40% !important; max-width:474px;
            p {padding:50px 50px 25px;}
            em {font-size:16px;}
            strong {font-size:40px; padding:10px 0 30px;}
            span {font-size:16px; line-height:26px; min-height:52px;}
            img {padding:0 25px;}
          }
        }
      }
    }
    .support_section {
      background-color:$white;
      ul {display:flex; justify-content:space-between;}
      li {
        width:346px; flex-wrap:nowrap;
        a {padding:40px 0 10px; display:block; text-align:center;}
        strong {
          padding:112px 0 20px; font-size:24px; color:$gray7; font-weight:$medium; display:block;
          @include backgroundSolt(50%, 0);
          @include transition(all, .3s);
        }
        span {font-size:14px; line-height:22px; display:block; color:$gray6;}
        &:nth-of-type(1) strong {background-image:url(../../images/main/support_icon01.png);}
        &:nth-of-type(2) strong {background-image:url(../../images/main/support_icon02.png);}
        &:nth-of-type(3) strong {background-image:url(../../images/main/support_icon03.png);}

        a:hover, a:focus {
          strong {color:$blue5; background-position:50% 10px;}
        }
      }
    }
    .explore_section {
      .tab_menu {
        margin:0 0 50px; text-align:center;
        li {
          margin:0 5px; display:inline-block;
          button {
            font-size:18px; line-height:54px; padding:0 25px; color:#8b8b8c; border-radius:50px; font-weight:$medium; background-color:$gray1;
            @include transition(all, .3s);
          }
          & button:hover, & button:focus, &.on button { color:$white; background-color:$gray7;}
        }
      }
      .tab_contents {
        .tab_content {
          display:none;
          &:first-child {display:block;}
        }
        .thumb_slide_wrap {
          margin:0 0 42px; text-align:center;
          .swiper-notification {display:none;}
          .thumb_slide_btn {
            display:inline-block; vertical-align:middle;
            button {
              @include textBackground(36px, 36px);
              @include backgroundSoltC();
            }
          }
          .thumb_slide_prev {background-image:url(../../images/main/slideBtn_prev.png)}
          .thumb_slide_next {background-image:url(../../images/main/slideBtn_next.png)}
          .swiper-button-disabled {
            opacity:0.4;
            button {cursor:default;}
          }
          .thumb_slide {
            max-width:715px; margin:0 10px 0 20px; display:inline-block; vertical-align:middle; overflow:hidden;
            .swiper-slide {width:120px;}
            .swiper-slide button {
              font-size:18px; line-height:41px; color:$gray7; font-weight:$medium; position:relative;
              &:before, &:after {
                width:0; height:3px; position:absolute; bottom:0; content:""; background-color:$gray7;
                @include transition(all, .3s);
              }
              &:before {left:50%;}
              &:after {right:50%;}
              &:hover:before, &:hover:after, &:focus:before, &:focus:after, &.active:before, &.active:after {width:50%;}
            }
          }
        }
        .ATM_cont {
          overflow:hidden;
          .photo {
            width:67.4%; max-width:798px; float:left;
            button {
              width:100%; border-radius:10px; overflow:hidden; position:relative;
              &:after {
                width:116px; height:116px; background-image:url(../../images/main/btn_play.png); position:absolute; top:50%; left:50%; content:"";
                @include translate(-50%, -50%);
                @include backgroundSoltC();
              }
              img {width:100%;}
            }
            ul {margin:6px 0 0; overflow:hidden;}
            li {
              width:calc(50% - 3px); border-radius:10px; float:left; overflow:hidden;
              &:last-child {float:right;}
              img {width:100%;}
            }
          }
          .info {
            width:32.6%; max-width:394px; min-height:727px; border-radius:10px; padding:70px 55px 50px; box-sizing:border-box; background-color:$white; float:right;
            p {
              color:$gray7;
              strong {font-size:40px; font-weight:$medium;}
              span {font-size:18px; line-height:26px; margin:20px 0 0; display:block; font-weight:$medium;}
            }
            > span {font-size:14px; line-height:20px; margin:40px 0 0; color:$gray6; display:block;}
            ul {margin:50px 0 0; display:flex; flex-wrap:wrap;}
            li {
              width:109px; margin:0 20px 20px 0; text-align:center;
              span {display:block;}
              p {font-size:12px; line-height:14px; color:$gray6}
            }
          }
        }
        .parts {
          margin:50px 0 0;
          > strong {font-size:20px; margin:0 0 30px; color:$gray7; font-weight:$medium; display:block; text-align:center;}
          .parts_slide {
            .swiper-slide {
              width:195px !important; margin:0 10px 0 0;
              a {padding:15px 20px 20px; border-radius:10px; overflow:hidden; display:block; background-color:$white; border:1px solid $gray2; box-sizing:border-box;}
              strong {font-size:18px; font-weight:$medium; color:$black; display:block; text-overflow:ellipsis; white-space:nowrap; overflow:hidden;}
              div {
                margin:10px 0 0;
                dl {
                  padding:0 0 0 70px; font-size:14px; line-height:18px; margin:5px 0 0; position:relative;
                  &:first-of-type {margin:0;}
                }
                dt {color:$gray5; position:absolute; top:0; left:0;}
                dd {color:$black;}
              }
              img {width:100%; margin:10px 0 0;}
            }
          }
        }
        .popup {
          .popup_content {max-width:1200px;}
          .frame_wrap {
            padding:56.25% 0 0; position:relative;
            iframe {width:100%; height:100%; position:absolute; top:0; left:0;}
          }
          .btn_popup_close {
            top:-80px; right:-80px; background-color:$white; border-radius:100%;
            &:after{
              width:20px; height:20px; background-image:url(../../images/common/close_x_btn.png); content:"";
              @include posCenterSolt(50%, 50%);
              @include backgroundSoltC();
              @include translate(-50%, -50%);
            }
          }
        }
      }
    }

    @include mob(){
      .title_wrap {
        padding:0 0 em(50);
        h3 {font-size:em(58); line-height:em(64); font-weight:$bold;}
      }
      section {padding:em(50) em(30);}
      .default_section {
        padding:em(50) em(30);
        > div:nth-of-type(3) {padding:em(120) 0 em(50);}
        .swiper-slide {/*max-width:em(586);*/max-width:85%; margin:0 em(24) 0 0;}
        .item {
          a {border-radius:em(20);}
          p {padding:em(55) em(60) em(15);}
          strong {font-size:em(56); font-weight:$bold;}
          span {font-size:em(32); line-height:em(42); min-height:em(84); margin:em(25) 0 0;}
        }
      }
      .banner_section {
        padding:em(50) 0; background-image:url(../../images/main/banner_bg_m.jpg);
        strong {
          font-size:em(56); line-height:em(60); font-weight:$bold;
          span {display:block; font-size:em(36); margin:em(15) 0 0;}
        }
        p {
          font-size:em(28); line-height:em(32); padding:0; margin:em(20) 0 0;
          em {display:block; position:static; margin:em(60) 0 0;}
          br {display:block;}
        }
      }
      .Preferred_section {
        padding:em(50) em(30); overflow-x:hidden;
        .layout_wrap {overflow:visible;}
        .swiper-slide {/*max-width:em(586);*/max-width:85%; margin:0 em(24) 0 0;}
        .item_list_new {
          .item {
            a {border-radius:em(20);}
            p {padding:em(55) em(60) em(15);}
            strong {font-size:em(56); font-weight:$bold; padding:em(15) 0 0;}
            span {font-size:em(32); line-height:em(42); min-height:em(84); margin:em(25) 0 0;}
          }
        }
      }
      .support_section {
        ul {display:block;}
        li {
          width:100%;
          a {
            padding:em(20) 0 em(20) em(235); text-align:left; position:relative;
            &:before {
              width:em(120); height:em(120); position:absolute; top:50%; left:em(30); content:"";
              @include translate(0, -50%);
              @include backgroundSoltC();
            }
          }
          strong {
            padding:0 0 em(20); font-size:24px; font-weight:$bold; background:none !important; letter-spacing:em(-2);
          }
          span {
            font-size:em(30); line-height:em(36);
            br {display:none;}
          }
          &:nth-of-type(1) a:before {background-image:url(../../images/main/support_icon01_m.png);}
          &:nth-of-type(2) a:before {background-image:url(../../images/main/support_icon02_m.png);}
          &:nth-of-type(3) a:before {background-image:url(../../images/main/support_icon03_m.png);}
        }
      }
      .explore_section {
        padding:em(50) 0;
        .tab_menu {

          margin:0 0 em(60);
          li {
            margin:0;
            button {
              font-size:em(30); line-height:em(90); padding:0 em(40); border-radius:em(50);
            }
          }
        }
        .tab_contents {
          .thumb_slide_wrap {
            margin:0; overflow:hidden; position:relative; z-index:1;
            &:after {width:100%; height:em(2); position:absolute; bottom:0; left:0; content:""; background-color:$gray2; z-index:-1}
            .thumb_slide_btn {display:none;}
            .thumb_slide {
              max-width:100%; margin:0; padding:0 0 0 em(70); box-sizing:border-box;
              .swiper-slide {width:auto; margin:0 em(40) 0 0;}
              .swiper-slide button {
                font-size:em(32); line-height:em(32); padding:0 0 em(30); font-weight:$bold;
                &:before, &:after {height:em(4);}
              }
            }
          }
          .ATM_cont {
            overflow:hidden; padding:em(30) em(30) 0;
            .photo {
              width:100%; max-width:100%;
              button {
                width:100%; border-radius:em(15);
                &:after {width:em(140); height:em(140);}
              }
              ul {margin:em(8) 0 0;}
              li {width:calc(50% - 0.2rem); border-radius:em(15);}
            }
            .info {
              width:100%; max-width:100%; min-height:auto; margin:em(8) 0 0; border-radius:em(15); padding:em(45) 0 em(55);
              p {
                padding:0 em(40);
                strong {font-size:em(48); font-weight:$bold;}
                span {font-size:em(30); line-height:em(34); margin:em(15) 0 0; font-weight:$bold;}
              }
              > span {font-size:14px; line-height:20px; margin:em(40) 0 0;}
              ul {margin:em(30) 0 0; overflow-x:auto; flex-wrap:nowrap}
              li {
                width:auto; margin:0;
                img {width:em(96);}
                p {font-size:12px; line-height:14px;}
              }
            }
          }
          .parts {
            margin:50px 0 0;
            > strong {font-size:20px; margin:0 0 30px; color:$gray7; font-weight:$medium; display:block; text-align:center;}
            .parts_slide {
              .swiper-slide {
                width:195px !important; margin:0 10px 0 0;
                a {padding:15px 20px 20px; border-radius:10px; overflow:hidden; display:block; background-color:$white; border:1px solid $gray2; box-sizing:border-box;}
                strong {font-size:18px; font-weight:$medium; color:$black; display:block; text-overflow:ellipsis; white-space:nowrap; overflow:hidden;}
                div {
                  margin:10px 0 0;
                  dl {
                    padding:0 0 0 70px; font-size:14px; line-height:18px; margin:5px 0 0; position:relative;
                    &:first-of-type {margin:0;}
                  }
                  dt {color:$gray5; position:absolute; top:0; left:0;}
                  dd {color:$black;}
                }
                img {width:100%; margin:10px 0 0;}
              }
            }
          }
          .popup {
            .popup_content {max-width:1200px;}
            .frame_wrap {
              padding:56.25% 0 0; position:relative;
              iframe {width:100%; height:100%; position:absolute; top:0; left:0;}
            }
            .btn_popup_close {
              top:-80px; right:-80px; background-color:$white; border-radius:100%;
              &:after{
                width:20px; height:20px; background-image:url(../../images/common/close_x_btn.png); content:"";
                @include posCenterSolt(50%, 50%);
                @include backgroundSoltC();
                @include translate(-50%, -50%);
              }
            }
          }
        }
      }
    }
  }
}

/* financial */
.financial_container {
  .dashboard {
    padding:50px 0; background-color:$gray7;
    h2 {font-size:40px; margin:0 0 30px; font-weight:$regular; color:$gold2; font-family:$bebasNeue;}
    .title {
      margin:0 0 30px; line-height:28px;
      @extend %clearfix;
      h3 {font-size:28px; font-weight:$medium; color:$white; float:left;}
      span {font-size:14px; color:$gray3; float:right; display:block;}
      .btn_refresh {
        margin:0 0 0 8px; vertical-align:middle; background-image:url(../../images/common/reset_icon.png);
        @include backgroundSoltC();
        @include textBackground(20px, 20px);
      }
    }
    .conts {
      a {
        font-size:20px; padding:0 20px 0 0; margin:0 0 20px; font-weight:$medium; display:inline-block; color:$gray7; position:relative;
        &:hover, &:focus {color:$blue5;}
        @include transition();
        &:after {
          width:18px; height:18px; position:absolute; top:1px; right:0; background-image:url(../../images/common/icon_arrow_tit.png); content:"";
          @include backgroundSoltC();
        }
      }
      .cont01 {
        display:flex; justify-content:space-between;
        > div {padding:30px; background-color:$white; border-radius:10px; box-sizing:border-box;}
        .chart_wrap {flex-basis:274px;}
        .status_wrap {
          width:calc(100% - 568px);
          > a {margin:0 0 10px;}
          ul {width:calc(100% + 4px); display:flex; flex-wrap:wrap; text-align:center; justify-content:center;}
          li {
            width:111px; margin:0 4px 0 0;
            em {
              font-size:12px; line-height:26px; margin:30px 0 15px; border-radius:5px; font-weight:$medium; display:block;
              &.status_type01 {color:$gray5; background-color:#ebebeb;}
              &.status_type02 {color:$green1; background-color:#eff5e7;}
              &.status_type03 {color:$green; background-color:#eaf6ed;}
              &.status_type04 {color:$blue; background-color:#ecf3fe;}
              &.status_type05 {color:$red; background-color:#feecea;}
            }
            strong {font-size:23px; font-weight:$bold; color:$gray7; display:block;}
          }
        }
      }
      .cont02 {
        margin:10px 0 0; padding:30px; background-color:$white; border-radius:10px; box-sizing:border-box;
        .table_wrap {
          table {width:100%;}
          thead th {font-size:13px; text-align:left; color:$gray5; padding:12px 0;}
          tbody td {
            font-size:14px; padding:7px 0; height:25px; color:$gray7; border-bottom:1px solid $gray1;
            span {
              min-width:50px; padding:0 5px; font-size:12px; line-height:23px; text-align:center; border-radius:5px; display:inline-block; border:1px solid $gray2;
              &.due_type01 {color:$blue5; font-weight:$bold;}
              &.due_type02 {color:$gray5; font-weight:$medium;}
            }
          }
          thead th:first-child, tbody td:first-child {padding:0 0 0 30px;}
        }
      }
    }
  }
  #contents {
    .title_wrap {
      max-width:1200px; margin:0 auto 40px;
      h3 {font-size:30px; font-weight:$regular; color:$gray7; font-family:$bebasNeue;}
    }
    .shortcut_section {
      max-width:1200px; margin:0 auto; padding:80px 0;
      ul {
        width:100%; display:flex;
        li {
          width:14.28%; border:1px solid $gray2; box-sizing:border-box; display:flex; margin:0 0 0 -1px;
          &:first-child {margin:0;}
          &.menu_icon01 a {
            &:after {background-image:url(../../images/common/shortcut_icon01.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon01_on.png)}
          }
          &.menu_icon02 a {
            &:after {background-image:url(../../images/common/shortcut_icon02.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon02_on.png)}
          }
          &.menu_icon03 a {
            &:after {background-image:url(../../images/common/shortcut_icon03.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon03_on.png)}
          }
          &.menu_icon04 a {
            &:after {background-image:url(../../images/common/shortcut_icon04.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon04_on.png)}
          }
          &.menu_icon05 a {
            &:after {background-image:url(../../images/common/shortcut_icon05.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon05_on.png)}
          }
          &.menu_icon06 a {
            &:after {background-image:url(../../images/common/shortcut_icon06.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon06_on.png)}
          }
          &.menu_icon07 a {
            &:after {background-image:url(../../images/common/shortcut_icon07.png)}
            &:hover:after, &:focus:after {background-image:url(../../images/common/shortcut_icon07_on.png)}
          }
        }
        a {
          width:100%; min-height:169px; padding:30px 25px 30px 30px; box-sizing:border-box; display:block; position:relative;
          @include transition();
          &:after {
            width:68px; height:68px; position:absolute; bottom:25px; right:25px; content:"";
            @include backgroundSolt();
          }
          strong {
            font-size:16px; line-height:20px; padding:0 0 5px; position:relative; font-weight:$medium; color:$gray7; display:block;
            @include transition();
            &:after {
              width:16px; height:16px; position:absolute; bottom:-18px; left:0; background-image:url(../../images/main/icon_arrow_s.png); content:"";
              @include backgroundSoltC();
              @include transition();
            }
          }
          &:hover, &:focus {
            background-color:$blue5; border-radius:20px;
            @include scale(1.05);
            @include boxShadow(0 0 20px rgba(15, 13, 13, 0.15));
            strong {
              color:$white;
              &:after {background-image:url(../../images/main/icon_arrow_s_on.png)}
            }
          }
        }
      }
    }
    .ordered_section {
      padding:80px 0; background-color:$gray1; overflow-x:hidden;
      .cont {
        margin:80px 0 0;
        &:first-child {margin:0}
      }
      .slide_wrap {max-width:1200px; margin:0 auto; overflow:visible; box-sizing:border-box;}
      .order_slide_type01 {
        .swiper-slide {max-width:410px; margin:0 20px 0 0;}
        .item {
          padding:40px; background-color:$white; border-radius:20px; border:1px solid $gray2; box-sizing:border-box;
          //@include boxShadow(0px 10px 35px rgba(18, 19, 20, 0.1));
          .info {
            position:relative; margin:0 0 25px;
            > a {
              font-size:30px; color:$gray7; font-weight:$medium;
              &:hover {text-decoration:underline;}
            }
            div {
              padding:25px 0 0;
              dl {
                font-size:17px; margin:10px 0 0; padding:0 0 0 90px; position:relative;
                &:first-child {margin:0;}
                dt {position:absolute; top:0; left:0; color:$gray5;}
                dd {color:$gray7;}
                strong {font-weight:$medium}
                em {
                  font-weight:$medium;
                  &.state_type01 {color:$green;}
                  &.state_type02 {color:$red;}
                  &.state_type03 {color:$blue;}
                  &.state_type04 {color:$green1;}
                }
              }
            }
            .btn_detail {
              font-size:15px; line-height:36px; width:123px; border-radius:30px; text-align:center; border:1px solid $gray2; color:$gray6; position:absolute; top:0; right:0;
              @include transition();
              &:hover, &:focus {color:$white; border-color:$blue5; background-color:$blue5}
            }
          }
          .details {
            padding:30px; border-top:1px solid $gray2; position:relative;
            img {width:100%;}
            .options {
              position:absolute; top:30px; left:0; width:100%; height:calc(100% - 60px); background-color:$white;
              div {
                margin:25px 0 0;
                &:first-child {margin:0;}
                strong {font-size:15px; font-weight:$medium; color:$gray5;}
                ul {margin:20px 0 0;}
                li {
                  font-size:15px; line-height:20px; margin:7px 0 0; padding:0 0 0 20px; color:$gray7; position:relative;
                  &:first-child {margin:0;}
                  &:before {width:3px; height:3px; background-color:$gray7; content:""; border-radius:100%; position:absolute; top:9px; left:6px;}
                }
              }
            }
          }
          .btn_reorder {
            font-size:16px; line-height:46px; border-radius:10px; display:block; text-align:center; font-weight:$bold; display:block; color:$blue5; background-color:$blue1;
            @include transition();
            &:hover, &:focus {background-color:$blue5; color:$white;}
          }
        }
      }
      .order_slide_type02 {
        .swiper-slide {max-width:298px; margin:0 16px 0 0;}
        .item {
          padding:30px; background-color:$white; border-radius:20px; border:1px solid $gray2; box-sizing:border-box;
          .info {
            > a {
              font-size:23px; color:$gray7; font-weight:$medium;
              &:hover {text-decoration:underline;}
            }
            div {
              padding:25px 0 20px;
              dl {
                font-size:17px; line-height:22px; margin:5px 0 0; padding:0 0 0 90px; position:relative;
                &:first-child {margin:0;}
                dt {position:absolute; top:0; left:0; color:$gray5;}
                dd {color:$gray7;}
                strong {font-weight:$medium}
                em {
                  font-weight:$medium;
                  &.state_type01 {color:$green;}
                  &.state_type02 {color:$red;}
                  &.state_type03 {color:$blue;}
                  &.state_type04 {color:$green1;}
                }
              }
            }
            img {width:100%}
            .btn_reorder {
              font-size:16px; line-height:46px; border-radius:10px; margin:25px 0 0; text-align:center; font-weight:$bold; display:block; color:$blue5; background-color:$blue1;
              @include transition();
              &:hover, &:focus {background-color:$blue5; color:$white;}
            }
          }
        }
      }
    }
  }
}